<template>
  <div class="title">
    <p class="title__task">
    <span v-if="this.todoItemsCount.total > 0 && this.todoItemsCount.left > 0">
      <span class="title__task-top">해야할일 총 <span class="total-count">{{this.todoItemsCount.total}}</span> 개중</span>
      <span class="title__task-bottom">
          <span class="remain-count">{{ this.todoItemsCount.left }}</span> 개가 남아있습니다.
      </span>
      <span class="title__task-info"></span>
    </span>
    <span v-else-if="this.todoItemsCount.total > 0 && this.todoItemsCount.left == 0">
        <span class="title__task-top">등록된 할일을 모두 마쳤어요!</span>
        <span class="title__task-bottom">할일을 등록해 보세요.</span>
    </span>
    <span v-else>
        <span class="title__task-top">할일이 없습니다.</span>
        <span class="title__task-bottom">할일을 등록해 보세요.</span>
    </span>
    </p>
  </div>
</template>

<script>

export default {
  data() {
    return {
    };
  },
  computed: {
    // 할 일 목록 수 표시
    todoItemsCount() {
      const checkLeftItems = () => {
        const items = this.$store.getters.storedTodoItems;
        let leftCount = 0;
        for (let i = 0; i < items.length; i++) {
          if (items[i].completed === false) {
            leftCount++;
          }
        }
        return leftCount;
      };

      const count = {
        total: this.$store.getters.storedTodoItemsCount,
        left: checkLeftItems()
      };
      return count;
    }
  },
};
</script>

