@import "./assets/style/_reset";

.top {
  width: 100%;
  min-height: 43.6rem;
  padding: 0 $padding 4.5rem;
  background-color: black;
}

.body {
  padding: 3rem $padding;
  background-color: #efefef;
}
</style>
